.html {
  height: 100%;
}

.welcome-container {
  color: rgb(7, 1, 14);
  height: 20%;
  display: flex;
  font-weight: 400;
  justify-content: center;
  padding-top: 6rem;
}

.login-container {
  height: 20rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: rgb(92, 91, 91);
}

.form-control {
  margin: 0.6rem 0 1rem 0;
}

.label-text {
  color: black;
  font-weight: 500;
}

.btn {
  font-size: 1.3rem;
  border: 1px solid grey;
}

.login-card-container {
  width: 100%;
  height: 40%;
  display: flex;
  padding-top: 2rem;
  padding-bottom: 4.5rem;
  background-color: rgb(32, 32, 32);
}

.login-card {
  width: 49% !important;
  border: none !important;
  text-align: center;
  background: none;
}

.login-card-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 6rem;
}

.main-text {
  width: 60%;
  margin-top: 1rem;
  padding: 1rem 1.5rem;
  background-color: aliceblue;
  font-weight: 500;
  font-size: 2rem;
  color: rgb(0, 0, 0);
}

.lorem {
  text-align:justify;
  width: 80%;
  margin-top: 2.5rem;
  padding: 0 2rem;
  font-weight: 300;
  font-size: 1.3rem;
  color: aliceblue;
}

#googleLogin {
  margin-top: 20px;
}

#gsi_566454_497689-wrapper {
  text-align: -moz-center;
}

@media (max-width: 992px) {
  .login-card-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .main-text {
    width: 100%;
    font-size: 1.5rem;
  }
  .lorem {
    width: 100%;
    padding: 0;
    font-size: 1rem;
  }
}